import { Component } from "../base/Component";
export class BasketView extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._title = this.container.querySelector('.modal__title');
        this._list = this.container.querySelector('.basket__list');
        this._total = this.container.querySelector('.basket__price');
        this._orderButton = this.container.querySelector('.basket__button');
        this._orderButton.addEventListener('click', () => {
            events.emit('order:open');
        });
        this.items = [];
    }
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
            this._title.textContent = 'Корзина';
        }
        else {
            this._list.replaceChildren(...items);
            this._title.textContent = 'Корзина пуста';
        }
    }
    set selected(items) {
        if (items.length) {
            this.setDisabled(this._orderButton, false);
        }
        else {
            this.setDisabled(this._orderButton, true);
        }
    }
    set total(total) {
        this.setText(this._total, `${total} синапсов`);
    }
}
