export class ProductModel {
    constructor(events) {
        this.events = events;
        this.basketItems = [];
        this.events = events;
    }
    set catalog(items) {
        this.catalogItems = items;
        this.events.emit('items:changed');
    }
    get catalog() {
        return this.catalogItems;
    }
    get count() {
        return this.basketItems.length;
    }
    get total() {
        return this.basketItems.reduce((total, item) => total + item.price, 0);
    }
    get basket() {
        return this.basketItems;
    }
    get basketItemsId() {
        return this.basketItems.map(item => item.id);
    }
    getItem(id) {
        return this.catalogItems.find(item => item.id === id);
    }
    isToBasket(id) {
        if (this.basketItems.filter(item => item.id === id).length)
            return true;
        else
            return false;
    }
    clearBasket() {
        this.events.emit('items:changed');
        return this.basketItems = [];
    }
    addToBasket(item) {
        this.basketItems.push(item);
        this.events.emit('items:changed');
    }
    deleteFromBasket(id) {
        this.basketItems = this.basketItems.filter(item => item.id !== id);
        this.events.emit('items:changed');
        return this.basketItems;
    }
    isPriceless(item) {
        if (item.price)
            return true;
        else
            return false;
    }
}
