import { ensureElement } from "../../utils/utils";
import { Component } from "../base/Component";
export class ProductCatalogView extends Component {
    constructor(container, events) {
        super(container);
        this.container = container;
        this.events = events;
        this.events = events;
        this._category = ensureElement('.card__category', this.container);
        this._title = ensureElement('.card__title', this.container);
        this._image = ensureElement('.card__image', this.container);
        this._price = ensureElement('.card__price', this.container);
        this.container.addEventListener('click', () => {
            this.events.emit('item:select', { id: this._id });
        });
    }
    set category(value) {
        this.setText(this._category, value);
        this.setColor(this._category, value);
    }
    set title(value) {
        this.setText(this._title, value);
    }
    set image(src) {
        this.setImage(this._image, src, this.title);
    }
    set price(value) {
        if (value === null)
            this.setText(this._price, `Бесценно`);
        else
            this.setText(this._price, `${value} синапсов`);
    }
    set id(value) {
        this._id = value;
    }
}
export class ProductPreviewView extends Component {
    constructor(container, events) {
        super(container);
        this.container = container;
        this.events = events;
        this.events = events;
        this._category = ensureElement('.card__category', this.container);
        this._title = ensureElement('.card__title', this.container);
        this._description = ensureElement('.card__text', this.container);
        this._image = ensureElement('.card__image', this.container);
        this._price = ensureElement('.card__price', this.container);
        this._addButton = ensureElement('.button_add', this.container);
        this._addButton.addEventListener('click', () => {
            this.events.emit('basket:add', { id: this._id });
        });
    }
    set category(value) {
        this.setText(this._category, value);
        this.setColor(this._category, value);
    }
    set title(value) {
        this.setText(this._title, value);
    }
    set description(value) {
        this.setText(this._description, value);
    }
    set image(src) {
        this.setImage(this._image, src, this.title);
    }
    set price(value) {
        if (value === null)
            this.setText(this._price, `Бесценно`);
        else
            this.setText(this._price, `${value} синапсов`);
    }
    set id(value) {
        this._id = value;
    }
    set button(value) {
        this.setText(this._addButton, value);
    }
    set isAdded(value) {
        if (value) {
            this.setDisabled(this._addButton, true);
            this.setText(this._addButton, 'Товар в корзине');
        }
        else
            this.setText(this._addButton, 'В корзину');
    }
    set selected(isPriceless) {
        if (isPriceless) {
            this.setDisabled(this._addButton, false);
            this.setText(this._addButton, 'В корзину');
        }
        else {
            this.setDisabled(this._addButton, true);
            this.setText(this._addButton, 'Товар не доступен');
        }
    }
    set deleteColor(value) {
        this.removeColor(this._category, value);
    }
}
