import { Component } from "../base/Component";
import { ensureElement } from "../../utils/utils";
export class SuccessView extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._description = ensureElement('.order-success__description', this.container);
        this._button = ensureElement('.order-success__close', this.container);
        this._button.addEventListener('click', () => {
            this.events.emit('order:close');
        });
    }
    set description(value) {
        this.setText(this._description, `Списано ${value} синапсов`);
    }
}
