import { Form } from "../common/Form";
export class OrderView extends Form {
    constructor(container, events) {
        super(container, events);
        this.events = events;
        this.onlineButton = this.container.querySelector('.button_online');
        this.getButton = this.container.querySelector('.button_get');
        this.inputField = this.container.querySelector('.form__input');
        this.submitButton = this.container.querySelector('.order__button');
        this.error = this.container.querySelector('.form__errors');
        this.formName = this.container.getAttribute('name');
        this.container.addEventListener('submit', (event) => {
            event.preventDefault();
            this.events.emit('forward:submit', { value: this.inputField.value });
        });
        this.onlineButton.addEventListener('click', () => {
            this.events.emit('payment:online');
        });
        this.getButton.addEventListener('click', () => {
            this.events.emit('payment:get');
        });
    }
    set value(value) {
        this.inputField.value = value;
    }
    set inputError(value) {
        this.setText(this.error, value);
    }
    set completedOnlineButton(value) {
        this.toggleClass(this.onlineButton, 'button_alt-active', value);
        this.toggleClass(this.getButton, 'button_alt-active', !value);
    }
    set completedOnfootButton(value) {
        this.toggleClass(this.onlineButton, 'button_alt-active', !value);
        this.toggleClass(this.getButton, 'button_alt-active', value);
    }
    set selected(value) {
        if (value) {
            this.setDisabled(this.submitButton, false);
        }
        else {
            this.setDisabled(this.submitButton, true);
        }
    }
    resetSettings() {
        this.value = '';
        this.completedOnlineButton = true;
        this.selected = false;
    }
}
export class ContactsView extends Form {
    constructor(container, events) {
        super(container, events);
        this.events = events;
        this.emailInputField = this.container.querySelector('.input_email');
        this.phoneInputField = this.container.querySelector('.input_phone');
        this.submitButton = this.container.querySelector('.button_pay');
        this.error = this.container.querySelector('.form__errors');
        this.formName = this.container.getAttribute('name');
        this.container.addEventListener('submit', (event) => {
            event.preventDefault();
            this.events.emit('order:submit');
        });
    }
    set emailValue(value) {
        this.emailInputField.value = value;
    }
    get emailValue() {
        return this.emailInputField.value;
    }
    set phoneValue(value) {
        this.phoneInputField.value = value;
    }
    get phoneValue() {
        return this.phoneInputField.value;
    }
    set inputError(value) {
        this.setText(this.error, value);
    }
    get inputError() {
        return this.error.textContent;
    }
    set selected(value) {
        if (value) {
            this.setDisabled(this.submitButton, false);
        }
        else {
            this.setDisabled(this.submitButton, true);
        }
    }
    resetSettings() {
        this.emailValue = '';
        this.phoneValue = '';
    }
}
