export class OrderModel {
    constructor(events) {
        this.events = events;
        this.order = {
            payment: '',
            address: '',
            email: '',
            phone: '',
            total: 0,
            items: []
        };
        this.events = events;
    }
    createOrder(object) {
        return {
            payment: this.payment,
            address: this.address,
            email: this.email,
            phone: this.phone,
            total: object.total,
            items: object.basketItemsId
        };
    }
    set payment(value) {
        this.order.payment = value;
    }
    get payment() {
        return this.order.payment;
    }
    set address(value) {
        this.order.address = value;
    }
    get address() {
        return this.order.address;
    }
    set email(value) {
        this.order.email = value;
    }
    get email() {
        return this.order.email;
    }
    set phone(value) {
        this.order.phone = value;
    }
    get phone() {
        return this.order.phone;
    }
}
